/* element css 重写 */
/* 弹框 */
.el-dialog__body{
  padding: 20px;
}
/* 行为分析url下拉框设置 */
.el-select-dropdown{
	max-width: 533px;
}
.el-select-dropdown__item{
	white-space: normal;
	height: auto;
	word-break: break-all;
	line-height: 20px;
	padding: 8px 20px;
}
.el-select-dropdown__item span{
	line-height:0px!important
}
/* 头部时间选择 */
.date-wrapper .el-input__inner{
	overflow: hidden;
}
/*页面多选框排列整齐 如配置属性等多选*/
.el-form-item__content{
	line-height: 38px;
}
.el-checkbox{
	margin-right: 30px;
}
.el-checkbox+.el-checkbox {
	margin-right: 30px;
	margin-left: 0px;
}
/* Tabs标签页*/
.el-tabs__header{
	margin: 0 0 20px;
}
.el-tabs__item{
	color: #606266;
	font-size: 16px;
}
.el-tabs__nav-wrap::after{
  background-color: #f1f1f1;
}
/* 分页 */
.el-pager li{
	font-weight: 400;
}
/* 复合型输入框 自动化配置 */
.el-input-group__append, .el-input-group__prepend{
  background-color: #fff;
}
/* el-dialog弹框框头部标题过长 */
.el-dialog__header .el-dialog__title{
  display: inline-block;
  width: 96%;
  word-break:break-all;
}
/* 输入框disable 时的字体颜色 */
.el-input.is-disabled .el-input__inner{
  color: #828080;
}
/* 折叠面板 */
.el-collapse{
  border-top: none;
  border-bottom: none;
}
.el-collapse-item__header{
  font-size: 14px;
  font-weight: 400;
  color: #606266;
  padding: 0 10px;
}
.el-collapse-item__header.is-active{
  font-weight: 700;
}
.el-collapse-item__content{
  color: #606266;
  padding: 0 20px 25px;
}
/*标签*/
.el-tag{
  color: #606266;
}
/* Tree 树形控件 */
.el-tree-node__content{
  height:auto;
}
.el-tree__empty-block{ /*暂无数据样式*/
  background: #f7f7f7;
  height:60px;
}
/* 按钮统一样式*/
.el-button--primary {
    color: #fff;
    background-color: #4a5166;
    border-color: #4a5166;
  }
.el-button--primary.is-active, .el-button--primary:active {
    background: #2f3648;
    border-color: #2f3648;
    color: #fff;
  }
.el-button--primary:focus, .el-button--primary:hover {
    background: #2f3648;
    border-color: #2f3648;
    color: #fff;
  }
.el-tabs__item.is-active {
    color: #2f3648;
  }
.el-switch.is-checked .el-switch__core {
    border-color: #34a01f;
    background-color: #34a01f;
  }
.el-range-editor.is-active, .el-range-editor.is-active:hover {
    border-color: #4a5166;
  }
.el-date-table td.end-date span, .el-date-table td.start-date span {
    background-color: #4a5166;
  }
.el-picker-panel__shortcut:hover {
    color: #2f3648;
  }
.el-button--success:focus, .el-button--success:hover {
    background: #2f3648;
    border-color: #2f3648;
    color: #fff;
  }
.el-button--success {
    color: #fff;
    background-color: #4a5166;
    border-color: #4a5166;
  }
.el-button--primary.is-disabled, .el-button--primary.is-disabled:active, .el-button--primary.is-disabled:focus, .el-button--primary.is-disabled:hover{
    background-color:#c6c8cb;
    border-color:#babec4;
  }
/* switch开关字体颜色 */
.el-switch__label{
    color:#606266
  }
.el-switch__label.is-active{
    color:#606266
  }
/* Notification 通知背景颜色 */
.el-notification{
    background: #d6dcee;
    cursor: pointer;
  }
/* textarea字体 */
.el-textarea__inner{
    font-family: auto;
  }
/* 左侧菜单滚动条 */
.aside-menu .el-scrollbar__wrap{
    overflow-x: hidden!important;
  }
